*/

.header h1 {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 60px;
  padding: 150px 0px 25px 0px;
}

.artwork-form-row {
	padding: 30px 0px;
}

.artwork-form-col {
	padding: 0px;
}

.form-group {
  margin-right: 20px;
  margin-top: 10px;
}

.form-group input {
	font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    margin: 5px 0px;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #8dcb51;
}

.form-group select {
	font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    margin: 5px 0px;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #8dcb51;
}

.form-group input:focus {
  outline: none;
  border-bottom: 2px solid #ce188e;
}

.form-group select:focus {
  outline: none;
  border-bottom: 2px solid #ce188e;
}

.form-group label {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 15px;
  margin: 0px 5px 0px 10px;
}

.sheet-info-container {
	text-align: center;
	margin: 50px 2px;
    padding: 25px;
    background-color: #dbf1c6;
    border-radius: 10px;
}

.sheet-info-container h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #404040;
}

.sheet-info-container p {
  font-family: 'Open Sans', sans-serif;
  color: #414141;
}

.sheet-info-error {
  background-color: #eee;
  text-align: center;
  margin: 50px 2px;
  padding: 25px;
  border-radius: 10px;
}

.sheet-info-error h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #ccc;
}

.sheet-info-error p {
  font-family: 'Open Sans', sans-serif;
  color: #414141;
  color: #ccc;
}

.eighteen-hundred-grid {
  display: grid;
  grid-template-columns: repeat(18, 10px);
  
}

.gottem-hide {
  display: none;
}

.gottem-img {
  text-align: center;
}


